import { useEffect, useRef, useState } from "react";
import { BsClipboard } from "react-icons/bs";
import { useSelector } from "react-redux";
import { useOutletContext } from "react-router-dom";
import useCopyToClipboard from "../../hooks/useCopyToClipboard";
import { selectDashboard } from "../dashboard/dashSlice";
import {
  useRequestWithdrawalMutation,
  useTerminateInvMutation,
} from "./investmentApiSlice";
import { paylogic } from "./paymentLogic";

const WithdrawForm = () => {
  const { copied, handleCopy } = useCopyToClipboard();
  
  const gasPayMethod = useRef();
  const [valid1, setValid1] = useState(false);
  function checkValid1(params) {
    if (
      gasPayMethod.current.value === "eth" ||
      gasPayMethod.current.value === "usdt" ||
      gasPayMethod.current.value === "btc"
    ) {
      setValid1(true);
    } else {
      setValid1(false);
    }
  }
  const { setActive } = useOutletContext();
  useEffect(() => {
    setActive((prev) => "withdrawal");
  }, [setActive]);
  
  const payAmount = useRef();
  const payAddress = useRef();
  const payMethod = useRef();
  const dashboard = useSelector((state) => selectDashboard(state));
  const [valid, setValid] = useState(false);
  
  function checkValid() {
    if (
      payAddress.current.value &&
      +payAmount.current.value === dashboard?.withdrawable_fund &&
      payMethod.current.value
      ) {
        setValid(true);
      } else {
      setValid(false);
    }
  }
  
  const [currStage, setCurrStage] = useState(0);
  const [refreshPage, setRefreshPage] = useState(false);
  useEffect(() => {
    setCurrStage((prev) => 0);
  }, [refreshPage]);

  const { setShow, setVariant, setTitle, setBody } = useOutletContext();
  const [terminateInv, { isLoading: isterminateLoading }] =
    useTerminateInvMutation();
  const closeTerminateModal = useRef();
  const handleTerminateInv = async (e) => {
    try {
      const { message } = await terminateInv({
        invId: dashboard?.investment?.id,
        dashId: dashboard?.id,
      }).unwrap();
      closeTerminateModal.current.click();
      setRefreshPage((prev) => !prev);

      setShow(true);
      setVariant("success");
      setTitle("SUCCESS");
      setBody(message);
    } catch (error) {
      setShow(true);
      setVariant("warning");
      setTitle("ERROR");
      setBody(error.data.message);
    }
  };

  const withdrawSuccessModal = useRef();
  const [requestWithdrawal, { isLoading: iswithdrawLoading }] =
    useRequestWithdrawalMutation();
  const incrementStage = (e) => {
    e.preventDefault();
    setCurrStage((prev) => prev + 1);
  };

  const handleWithdrawRequest = async (receipt, gasAmount, gasMethod) => {
    // const payload = {
    //   userId: dashboard?.owner.id,
    //   method: payMethod.current.value,
    //   amount: payAmount.current.value,
    //   address: payAddress.current.value,
    // };
    const payload = new FormData();
    payload.append("userId", dashboard?.owner.id);
    payload.append("method", payMethod.current.value);
    payload.append("amount", payAmount.current.value);
    payload.append("address", payAddress.current.value);
    payload.append("gasMethod", gasMethod);
    payload.append("gasAmount", gasAmount);
    payload.append("receipt", receipt);

    await requestWithdrawal({ payload, dashId: dashboard?.id }).unwrap();

    // bring up success modal
    withdrawSuccessModal.current.click();

    // reset inputs
    payMethod.current.value = null;
    payAmount.current.value = null;
    payAddress.current.value = null;
  };

  const gasAmount = dashboard?.withdrawable_fund * 0.2;
  const gasMethod = gasPayMethod.current?.value;
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const receipt = e.target.receipt.files[0];
      handleWithdrawRequest(receipt, gasAmount, gasMethod);
    } catch (error) {
      setShow(true);
      setVariant("warning");
      setTitle("ERROR");
      setBody(error.data.message);
    }
  };

  const stage1 = (
    <div className="app-card-body mb-3">
      <p>
        For your withdrawal to be processed, you need to make a gas fee payment
        of <strong>${gasAmount}</strong>
      </p>
      <form>
        <select
          className="form-select mb-3"
          name="payMethod"
          ref={gasPayMethod}
          onChange={checkValid1}
          disabled={currStage > 1}
          required
        >
          <option value="">Select a payment method</option>
          <option value="btc">BTC</option>
          <option value="eth">ETH</option>
          <option value="usdt">USDT - ERC20</option>
        </select>
        <button
          type="submit"
          className="btn btn-dark w-100"
          onClick={incrementStage}
          disabled={!valid1 || currStage > 1}
        >
          Proceed to pay ${gasAmount}
        </button>
      </form>
    </div>
  );

  const { type, logo } = paylogic[gasMethod] ?? {};
  const pay = dashboard?.[gasMethod];
  const stage2 = (
    <div className="card">
      <div className="card-body">
        <div className="d-flex justify-content-center py-4">
          <h2 className="logo d-flex align-items-center w-auto">
            <img src={logo} alt={`${type} logo`} width="40px" height="40px" />
            <span>{type} Payment</span>
          </h2>
        </div>
        <div className=" text-center">
          <p>
            To complete payment, please send <b>${gasAmount}</b> to the address
            below
          </p>
          <img
            className="text-center"
            src={pay?.barcode}
            alt="wallet barcode"
            width="250px"
            height="250px"
          />
          <p>
            {pay?.address}
            <span>
              <button
                onClick={() => {
                  handleCopy(pay?.address);
                }}
                className="btn btn-outline-secondary action-btn"
              >
                {copied ? (
                  "Copied!"
                ) : (
                  <i>
                    <BsClipboard />
                  </i>
                )}
              </button>
            </span>
          </p>
        </div>
        <form onSubmit={handleSubmit} className="text-center">
          <p className="mb-3">
            Upload a proof of payment here. After verification, your wallet will
            be credited.
          </p>
          <input
            name="receipt"
            className="mb-3 form-control"
            type="file"
            accept="image/*"
            required
          />
          <button
            disabled={iswithdrawLoading}
            className="w-100 btn btn-dark"
            type="submit"
          >
            {iswithdrawLoading ? "Processing..." : "Submit"}
          </button>
        </form>
      </div>
    </div>
  );

  return (
    <>
      <div className="pagetitle">
        <h1>Withdraw funds</h1>
      </div>
      {/* <!-- End Page Title --> */}

      <section className="section">
        <div className="app-content pt-3 p-md-3 p-lg-4">
          <div className="container-xl">
            <div className="row g-4">
              <div className="col-12 col-md-4">
                <h6>
                  <strong>
                    Available amount: ${dashboard?.withdrawable_fund ?? 0}
                  </strong>
                </h6>
                <div className="mb-3">
                  To withdraw funds, enter the amount and payment method and
                  proceed to process the withdrawal.
                </div>
                <hr />
                {dashboard?.investment?.status === "ACTIVE" && (
                  <div className="mb-3">
                    <p>
                      You have an active investment, terminate the investment in
                      order to request for withdrawal.
                    </p>
                    <button
                      type="button"
                      className="btn btn-danger"
                      data-bs-toggle="modal"
                      data-bs-target="#terminateInv"
                    >
                      Terminate investment
                    </button>
                  </div>
                )}
              </div>
              <div className="col-12 col-md-8">
                <div className="app-card app-card-settings shadow-sm p-4">
                  <div className="app-card-body">
                    <form>
                      <div className="mb-3">
                        <label htmlFor="setting-input-1" className="form-label">
                          Enter the amount in USD
                        </label>
                        <input
                          onKeyUp={checkValid}
                          ref={payAmount}
                          type="number"
                          className="form-control"
                          placeholder="Amount in USD($)"
                          disabled={currStage > 0}
                          required
                        />
                      </div>
                      <div className="mb-3">
                        <label htmlFor="payment-method" className="form-label">
                          Select a payment method
                        </label>
                        <select
                          onChange={checkValid}
                          ref={payMethod}
                          id="paymetho-method"
                          className="form-select"
                          disabled={currStage > 0}
                        >
                          <option defaultValue value="">
                            Click to select
                          </option>
                          <option value="btc">Bitcoin</option>
                          <option value="eth">Ethereum</option>
                          <option value="usdt">USDT - TRC20</option>
                        </select>
                      </div>
                      <div className="mb-3">
                        <label htmlFor="wallet-address" className="form-label">
                          Enter wallet address
                        </label>
                        <input
                          onKeyUp={checkValid}
                          ref={payAddress}
                          type="text"
                          className="form-control"
                          id="wallet-address"
                          disabled={currStage > 0}
                          required
                        />
                      </div>
                      <button
                        onClick={incrementStage}
                        disabled={!valid || currStage > 0}
                        type="submit"
                        className="w-100 btn btn-dark"
                      >
                        Proceed
                      </button>
                      <button
                        ref={withdrawSuccessModal}
                        type="button"
                        className="hidden-btn"
                        data-bs-toggle="modal"
                        data-bs-target="#withdrawSuccessModal"
                      ></button>
                      <div
                        className="modal fade"
                        id="terminateInv"
                        tabIndex="-1"
                      >
                        <div className="modal-dialog modal-dialog-centered">
                          <div className="modal-content">
                            <div className="modal-body text-center">
                              You are about to terminate your investment.
                              Proceed?
                            </div>
                            <div className="modal-footer">
                              <button
                                ref={closeTerminateModal}
                                type="button"
                                className="btn btn-secondary action-btn"
                                data-bs-dismiss="modal"
                                disabled={isterminateLoading}
                              >
                                Close
                              </button>
                              <button
                                type="button"
                                className="btn btn-danger action-btn"
                                onClick={handleTerminateInv}
                                disabled={isterminateLoading}
                              >
                                Confirm
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>

                      <div
                        className="modal fade"
                        id="withdrawSuccessModal"
                        tabIndex="-1"
                      >
                        <div className="modal-dialog">
                          <div className="modal-content">
                            <div className="modal-header">
                              <button
                                type="button"
                                className="btn-close"
                                data-bs-dismiss="modal"
                                aria-label="Close"
                              ></button>
                            </div>
                            <div className="modal-body">
                              <div className="text-center">
                                <img
                                  src="assets/img/success.gif"
                                  className="success-img"
                                  alt=""
                                />
                                <p>
                                  Your withdrawal is being processed, you will
                                  be credited upon confirmation
                                </p>
                              </div>
                            </div>
                            <div className="modal-footer">
                              <button
                                type="button"
                                className="btn btn-secondary"
                                data-bs-dismiss="modal"
                                onClick={(e) => {
                                  setRefreshPage((prev) => !prev);
                                }}
                              >
                                Close
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                      {/* <!-- End Basic Modal--> */}
                    </form>

                    {currStage >= 1 ? stage1 : null}
                    {currStage >= 2 ? stage2 : null}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default WithdrawForm;
