import React, { useEffect } from 'react'
import { useOutletContext } from 'react-router-dom'
import useAuth from '../../hooks/useAuth'
import { useGetTransactionHistoryQuery } from './investmentApiSlice'
import TransactionRow from './TransactionRow'

const History = () => {

  const { setActive } = useOutletContext()
  useEffect(() => {
    setActive(prev => 'history')
  }, [setActive])

  const { userId } = useAuth()
  const {
    data: transactions,
    isLoading,
    isSuccess,
    isError,
    error
  } = useGetTransactionHistoryQuery(userId, {
    pollingInterval: 5 * 60 * 1000,
    refetchOnMountOrArgChange: 60,
    // refetchOnFocus: true,
    // refetchOnReconnect: true
  })

  let content, tableContent

  if (isLoading) content = <p>Loading...</p>

  if (isError) {
    content = <p className="error-message">{error?.data?.message}</p>
  }

  if (isSuccess) {
    const { entities } = transactions

    tableContent = Object.values(entities).length
      ? Object.values(entities).map(trans => <TransactionRow key={trans.id} trans={trans} />)
      : <tr>
        <td className='text-center' colSpan={5}>No transactions found</td>
      </tr>

    content = <section className="section">
      <div className="col-12">
        <div className="card recent-sales overflow-auto">

          <table className="table table-borderless table-striped">
            <thead>
              <tr>
                <th scope="col">#ID</th>
                <th scope="col">Transaction type</th>
                <th scope="col">Date</th>
                <th scope="col">Amount</th>
                <th scope="col">Status</th>
              </tr>
            </thead>
            <tbody>
              {tableContent}
            </tbody>
          </table>

        </div>
      </div>
    </section>
  }

  return (
    <>
      <div className="pagetitle">
        <h1>Transaction History</h1>
      </div>
      {content}
    </>
  )
}

export default History