import React from 'react'

const TransactionRow = ({ trans }) => {
    const date = new Date(trans.createdAt)
    const formattedDate = new Intl.DateTimeFormat('en-US').format(date)

    const content = trans
        ? <tr>
            <td>{trans.id.substring(15)}</td>
            <td>{trans.type}</td>
            <td>{formattedDate}</td>
            <td>${trans.amount}</td>
            <td><span
                className={trans.completed ? "badge bg-success" : "badge bg-warning"}>
                {trans.completed ? 'Completed' : 'Pending'}
            </span></td>
        </tr>
        : null
    return (
        content
    )
}

export default TransactionRow