import { createEntityAdapter, createSelector } from "@reduxjs/toolkit/dist"
import { apiSlice } from "../../app/api/apiSlice"

const transactionAdapter = createEntityAdapter({})
const initialState = transactionAdapter.getInitialState()

export const investmentApiSlice = apiSlice.injectEndpoints({
    endpoints: builder => ({
        makeDeposit: builder.mutation({
            query: ({ payload }) => ({
                url: `/investment/deposit`,
                method: 'POST',
                body: payload
            }),
            invalidatesTags: (result, error, { dashId }) => [
                { type: 'Dashboard', id: dashId },
                { type: 'Transaction', id: 'LIST' }
            ],
        }),
        verifyDeposit: builder.mutation({
            query: ({ invId }) => ({
                url: `/investment/verify-deposit`,
                method: 'PATCH',
                body: { invId }
            }),
            invalidatesTags: (result, error, { dashId }) => [
                { type: 'Dashboard', id: dashId },
                { type: 'Transaction', id: 'LIST' }
            ],
        }),
        getTransactionHistory: builder.query({
            query: (userId) => `/investment/transaction-history/${userId}`,
            validateStatus: (response, result) => {
                return response.status === 200 && !result.isError
            },
            // keepUnusedDataFor: 60, // default
            transformResponse: responseData => {
                const transactions = responseData.map(transaction => {
                    transaction.id = transaction._id
                    return transaction
                })
                return transactionAdapter.setAll(initialState, transactions)
            },
            providesTags: (result, error, arg) => {
                if (result?.ids) {
                    return [
                        { type: 'Transaction', id: 'LIST' },
                        ...result.ids.map(id => ({ type: 'Transaction', id }))
                    ]
                } else return [{ type: 'Transaction', id: 'LIST' }]
            }
        }),
        terminateInv: builder.mutation({
            query: ({ invId }) => ({
                url: `/investment/terminate-investment`,
                method: 'PATCH',
                body: { invId }
            }),
            invalidatesTags: (result, error, { dashId }) => [
                { type: 'Dashboard', id: dashId }
            ],
        }),
        requestWithdrawal: builder.mutation({
            query: ({ payload }) => ({
                url: `/investment/request-withdrawal`,
                method: 'POST',
                body: payload
            }),
            invalidatesTags: (result, error, { dashId }) => [
                { type: 'Dashboard', id: dashId }
            ],
        }),
        getWithdrawDetails: builder.mutation({
            query: ({ userId }) => `/investment/withdrawal/${userId}`,
        }),
        confirmWithdrawal: builder.mutation({
            query: ({ transId, gasId }) => ({
                url: `/investment/confirm-withdrawal`,
                method: 'PATCH',
                body: { transId, gasId }
            }),
            invalidatesTags: (result, error, { dashId }) => [
                { type: 'Dashboard', id: dashId }
            ],
        }),
    }),
})

export const {
    useMakeDepositMutation,
    useVerifyDepositMutation,
    useGetTransactionHistoryQuery,
    useTerminateInvMutation,
    useRequestWithdrawalMutation,
    useGetWithdrawDetailsMutation,
    useConfirmWithdrawalMutation,
} = investmentApiSlice

export const selectTransactionResult = investmentApiSlice.endpoints.getTransactionHistory.select()

const selectTransactionData = createSelector(
    selectTransactionResult,
    transactionResult => transactionResult.data
)

export const {
    selectAll: selectAllTransactions,
    selectById: selectTransactionById,
    selectIds: selectTransactionIds
} = transactionAdapter.getSelectors(state => selectTransactionData(state) ?? initialState)
