import React from 'react'
import { Badge } from 'react-bootstrap'
import { BsBoxArrowUpRight, BsTrash } from 'react-icons/bs'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { selectDashboardById } from './dashboardApiSlice'

const DashUserRow = ({ dashId, setUserId }) => {
    const dash = useSelector(state => selectDashboardById(state, dashId))
    const issues = dash?.issues
    const issuesCount = issues?.length

    return (
        <tr>
            <td>{dash.owner?.firstName} {dash.owner?.lastName}</td>
            <td>{dash.owner?.email}</td>
            <td>
                <Badge
                    bg={dash.hasInvestment ? 'success' : 'dark'}>
                    {dash.hasInvestment ? 'Active' : 'Inactive'}
                </Badge>
            </td>
            <td>
                <Badge
                    bg={issuesCount ? "warning" : "dark"}>
                    {issues?.[0] || 'None'}
                </Badge>
                {issuesCount - 1 > 0 &&
                    <Badge
                        pill
                        bg="light" text='dark'>
                        {issuesCount}
                    </Badge>}
            </td>
            <td>
                <div className="action-btns">
                    <Link to={`/admin/profile/${dashId}`} title='Go to user profile' className='btn btn-outline-secondary action-btn'><i><BsBoxArrowUpRight /></i></Link>
                    <button onClick={e => { setUserId(prev => dash.owner.id) }} title='Delete user permanently' className='btn btn-outline-danger action-btn' data-bs-toggle="modal" data-bs-target="#deleteAction"><i><BsTrash /></i></button>
                </div>
            </td>
        </tr>
    )
}

export default DashUserRow