import React, { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import { useOutletContext, useParams } from "react-router-dom";

import {
  selectDashboardById,
  useVerifyUserMutation,
} from "./dashboardApiSlice";
import {
  useConfirmWithdrawalMutation,
  useGetWithdrawDetailsMutation,
  useMakeDepositMutation,
  useVerifyDepositMutation,
} from "../investment/investmentApiSlice";
import { selectDashboard } from "./dashSlice";
import ChangePasswordForm from "./ChangePasswordForm";
import KYCForm from "../../components/KYCForm";
import AvatarForm from "../../components/AvatarForm";
import useAuth from "../../hooks/useAuth";
import {
  selectAllWallets,
  useUpdateDashWalletMutation,
} from "./walletApiSlice";
import { BsPencilSquare } from "react-icons/bs";

const Profile = () => {
  const { id } = useParams();
  const { isAdmin } = useAuth();
  const fromAdmin = useSelector((state) => selectDashboardById(state, id));
  const fromUser = useSelector((state) => selectDashboard(state));
  const closeModal = useRef();
  const dash = fromAdmin ?? fromUser;
  const investment = dash?.hasInvestment ? dash?.investment : null;

  const [walletType, setWalletType] = useState("");
  const wallets = useSelector((state) => selectAllWallets(state));
  const walletOptions = wallets.length
    ? wallets
        .filter((wallet) => wallet.type === walletType)
        .map((wallet) => {
          return (
            <option key={wallet.id} value={wallet.id}>
              {wallet.address}
            </option>
          );
        })
    : null;

  const [verifyUser, { isLoading }] = useVerifyUserMutation();
  const [verifyDeposit, { isLoading: verifyLoading }] =
    useVerifyDepositMutation();
  const [updateDashWallet, { isLoading: updateLoading }] =
    useUpdateDashWalletMutation();
  const [makeDeposit, { isLoading: createInvLoading }] =
    useMakeDepositMutation();

  const { setActive, setShow, setVariant, setTitle, setBody } =
    useOutletContext();
  useEffect(() => {
    if (!isAdmin) {
      setActive((prev) => "profile");
    }
  }, [setActive, isAdmin]);

  let [
    getWithdrawDetails,
    {
      isLoading: isWithdrawDetailsLoading,
      isSuccess: isWithdrawDetailsSuccess,
      isError: isWithdrawDetailsError,
      error: withdrawDetailsError,
      data: transDetails,
    },
  ] = useGetWithdrawDetailsMutation();
  console.log(dash?.issues);

  let withdrawContent;

  if (isWithdrawDetailsLoading) withdrawContent = <p>Loading...</p>;

  if (!transDetails) {
    withdrawContent = <p>Could not find details for the request.</p>;
    // console.log(transDetails)
  }

  if (isWithdrawDetailsSuccess && transDetails) {
    withdrawContent = (
      <>
        <div className="card">
          <strong>Amount:</strong> ${transDetails?.amount}
        </div>
        <div className="card">
          <strong>Payment method:</strong> {transDetails?.method.toUpperCase()}
        </div>
        <div className="card">
          <strong>Wallet Address:</strong> {transDetails?.walletAddress}
        </div>
        <div className="card">
          <p><strong>Gas fee receipt</strong></p>
          {/* <img src= {transDetails?.gas}/> */}
        </div>
      </>
    );
  }

  if (isWithdrawDetailsError) {
    withdrawContent = (
      <p>An error occured while fetching the request details.</p>
    );
    console.log(withdrawDetailsError);
  }

  const fetchWithdrawDetails = async (e) => {
    await getWithdrawDetails({ userId: dash?.owner.id });
  };

  const handleVerifyUser = async () => {
    try {
      const { message } = await verifyUser({ userId: dash?.owner.id }).unwrap();

      setVariant("success");
      setTitle("SUCCESS");
      setBody(message);
      setShow(true);
    } catch (error) {
      setShow(true);
      setVariant("warning");
      setTitle("ERROR");
      setBody(error.data.message);
    }
  };

  const handleVerifyDeposit = async () => {
    try {
      const { message } = await verifyDeposit({
        dashId: dash?.id,
        invId: investment?.id,
      }).unwrap();

      setVariant("success");
      setTitle("SUCCESS");
      setBody(message);
      setShow(true);
    } catch (error) {
      setShow(true);
      setVariant("warning");
      setTitle("ERROR");
      setBody(error.data.message);
    }
  };

  const handleUpdateWallet = async (e) => {
    e.preventDefault();
    try {
      const payload = {
        userId: dash?.owner.id,
        walletId: e.target.wallet.value,
        type: walletType,
      };
      const { message } = await updateDashWallet({ payload }).unwrap();

      setVariant("success");
      setTitle("SUCCESS");
      setBody(message);
      setShow(true);

      closeModal.current.click();
    } catch (error) {
      setShow(true);
      setVariant("warning");
      setTitle("ERROR");
      setBody(error.data.message);
    }
  };

  const [gasId, setGasId] = useState("");
  useEffect(() => {
    if (transDetails?.receipt) {
      const result = transDetails.receipt.match(/[0-9a-z]{24}/);
      setGasId((prev) => result?.[0]);
    }
  }, [transDetails]);

  const [confirmWithdrawal, { isLoading: isCWLoading }] =
    useConfirmWithdrawalMutation();
  const handleCompleteWithdraw = async (e) => {
    try {
      const transId = transDetails?.id;
      const { message } = await confirmWithdrawal({
        transId,
        gasId,
        dashId: dash?.id,
      }).unwrap();

      transDetails = undefined;

      setVariant("success");
      setTitle("SUCCESS");
      setBody(message);
      setShow(true);
    } catch (error) {
      setShow(true);
      setVariant("warning");
      setTitle("ERROR");
      setBody(error.data.message);
    }
  };

  if (!dash) return <p>Loading...</p>;

  const handleCreateInv = async (e) => {
    e.preventDefault();
    const payload = new FormData();
    payload.append("userId", dash?.owner.id);
    payload.append("email", dash?.owner.email);
    payload.append("invPackage", e.target.invPackage);
    payload.append("plan", e.target.invPlan);
    payload.append("amount", e.target.amount);
    payload.append("method", e.target.payMethod);

    try {
      const { message } = await makeDeposit({
        payload,
        dashId: dash?.id,
      }).unwrap();

      setShow(true);
      setVariant("success");
      setTitle("SUCCESS");
      setBody(message);

      e.target.invPackage.value = null;
      e.target.invPlan.value = null;
      e.target.payMethod.value = null;
      e.target.amount.value = null;
    } catch (error) {
      setShow(true);
      setVariant("warning");
      setTitle("ERROR");
      setBody(error.data?.message);
    }
  };

  const adminActions = isAdmin ? (
    <>
      <div className="row">
        <div className="col-lg-3 col-md-4 label">Investment Amount</div>
        <div className="col-lg-9 col-md-8">
          {dash?.investment?.investmentAmount ?? "N/A"}
        </div>
      </div>

      <div className="row">
        <div className="col-lg-3 col-md-4 label">Profit</div>
        <div className="col-lg-9 col-md-8">
          {dash?.investment?.ROI ?? "N/A"}
        </div>
      </div>

      <div className="row">
        <div className="col-lg-3 col-md-4 label">Deposit Verification</div>
        <div className="action-btns col-lg-9 col-md-8">
          <button
            className="btn action-btn btn-outline-secondary"
            data-bs-toggle="modal"
            data-bs-target="#depositModal"
          >
            View receipt
          </button>
          <button
            disabled={verifyLoading}
            onClick={handleVerifyDeposit}
            className="btn action-btn btn-outline-secondary"
          >
            {verifyLoading ? "Verifying..." : "Verify"}
          </button>
        </div>
      </div>

      <div className="row">
        <div className="col-lg-3 col-md-4 label">BTC Address</div>
        <div className="col-lg-9 col-md-8">
          {dash?.btc?.address}
          <button
            onClick={(e) => {
              setWalletType("btc");
            }}
            className="btn action-btn btn-outline-secondary"
            title="Edit wallet"
            data-bs-toggle="modal"
            data-bs-target="#walletUpdateModal"
          >
            <i>
              <BsPencilSquare />
            </i>
          </button>
        </div>
      </div>

      <div className="row">
        <div className="col-lg-3 col-md-4 label">ETH Address</div>
        <div className="col-lg-9 col-md-8">
          {dash?.eth?.address}
          <button
            onClick={(e) => {
              setWalletType("eth");
            }}
            className="btn action-btn btn-outline-secondary"
            title="Edit wallet"
            data-bs-toggle="modal"
            data-bs-target="#walletUpdateModal"
          >
            <i>
              <BsPencilSquare />
            </i>
          </button>
        </div>
      </div>

      <div className="row">
        <div className="col-lg-3 col-md-4 label">USDT Address</div>
        <div className="col-lg-9 col-md-8">
          {dash?.usdt?.address}
          <button
            onClick={(e) => {
              setWalletType("usdt");
            }}
            className="btn action-btn btn-outline-secondary"
            title="Edit wallet"
            data-bs-toggle="modal"
            data-bs-target="#walletUpdateModal"
          >
            <i>
              <BsPencilSquare />
            </i>
          </button>
        </div>
      </div>

      <div className="modal fade" id="walletUpdateModal" tabIndex="-1">
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">
                Change {walletType.toUpperCase()} Wallet
              </h5>
              <button
                ref={closeModal}
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">
              <form onSubmit={handleUpdateWallet}>
                <select
                  className="form-select mb-3"
                  name="wallet"
                  id="wallet"
                  required
                >
                  <option value="">Select a wallet</option>
                  {walletOptions}
                </select>
                <div className="modal-footer">
                  <button
                    disabled={updateLoading}
                    type="submit"
                    className="btn btn-secondary"
                  >
                    {updateLoading ? "Updating..." : "Update"}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>

      <div className="row">
        <div className="col-lg-3 col-md-4 label">Withdrawal Request</div>
        <div className="action-btns col-lg-9 col-md-8">
          <button
            onClick={fetchWithdrawDetails}
            className="btn action-btn btn-outline-secondary"
            data-bs-toggle="modal"
            data-bs-target="#viewWithdrawModal"
          >
            View Request
          </button>
          <button
            onClick={handleCompleteWithdraw}
            disabled={!transDetails | isCWLoading}
            className="btn action-btn btn-outline-secondary"
          >
            Complete
          </button>
        </div>
      </div>

      <div className="row">
        <div className="col-lg-3 col-md-4 label">KYC verification</div>
        <div className="action-btns col-lg-9 col-md-8">
          <button
            className="btn action-btn btn-outline-secondary"
            data-bs-toggle="modal"
            data-bs-target="#idModal"
          >
            View ID
          </button>
          <button
            onClick={handleVerifyUser}
            disabled={isLoading || dash?.owner.verified}
            className={
              dash?.owner.verified
                ? `btn action-btn btn-success`
                : `btn btn-outline-secondary`
            }
          >
            {dash?.owner.verified ? "Verified" : "Verify"}
          </button>
        </div>
      </div>
      <div className="modal fade" id="viewWithdrawModal" tabIndex="-1">
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">Withdrawal Request</h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">{withdrawContent}</div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="modal fade" id="idModal" tabIndex="-1">
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">Front/Back ID</h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">
              <div className="card">
                <img src={dash?.owner.IDFront} alt="" />
              </div>
              <div className="card">
                <img src={dash?.owner.IDBack} alt="" />
              </div>
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="modal fade" id="depositModal" tabIndex="-1">
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">Deposit receipt</h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">
              <img
                src={investment?.transaction?.receipt ?? ""}
                alt="proof of payment"
              />
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="">
        <button
          className="btn btn-dark w-50"
          data-bs-toggle="modal"
          data-bs-target="#addInv"
        >
          Add User's Investment
        </button>
      </div>
      <div className="modal fade" id="addInv" tabIndex="-1">
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">Update User's Investment</h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">
              <form onSubmit={handleCreateInv}>
                <div className="mb-3">
                  <select
                    className="form-select"
                    name="invPlan"
                    id="invPlan"
                    required
                  >
                    <option value="">Select an Investment plan</option>
                    <option value="basic">BASIC</option>
                    <option value="premium">PREMIUM</option>
                    <option value="vip">VIP</option>
                  </select>
                </div>

                <div className="mb-3">
                  <select
                    className="form-select"
                    name="invPackage"
                    id="invPackage"
                    required
                  >
                    <option value="">Select an Investment package</option>
                    <option value="agric">AGRICULTURE</option>
                    <option value="forex">FOREX</option>
                    <option value="crypto">CRYPTOCURRENCY</option>
                    <option value="realEstate">REAL ESTATE</option>
                    <option value="energy">ENERGY</option>
                    <option value="inheritance">INHERITANCE</option>
                    <option value="metals">METALS</option>
                  </select>
                </div>
                <div className="mb-3">
                  <select
                    className="form-select"
                    name="payMethod"
                    id="payMethod"
                    required
                  >
                    <option value="">Select a Payment method</option>
                    <option value="btc">Bitcoin</option>
                    <option value="eth">Ethereum</option>
                    <option value="usdt">USDT</option>
                  </select>
                </div>
                <div className="mb-3">
                  <input
                    type="number"
                    name="amount"
                    className="form-control"
                    placeholder="Investment Amount"
                  />
                </div>
                <button className="btn btn-dark" disabled={createInvLoading}>
                  {createInvLoading ? "Submitting..." : `Submit`}
                </button>
              </form>
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  ) : null;

  return (
    <>
      <div className="pagetitle">
        <h1>Account Profile</h1>
      </div>

      <section className="section profile">
        <div className="row">
          <div className="col-xl-4">
            {/* <!-- End Basic Modal--> */}
            <div className="card">
              <div className="card-body profile-card pt-4 d-flex flex-column align-items-center">
                <img
                  src={dash?.avatar ?? "/assets/img/no-avatar.png"}
                  alt="Profile"
                  className="rounded-circle mb-3"
                  width={"120px"}
                  height={"120px"}
                />
                {!isAdmin && (
                  <button
                    type="button"
                    className="btn btn-outline-secondary"
                    data-bs-toggle="modal"
                    data-bs-target="#basicModal"
                  >
                    Change photo
                  </button>
                )}
                <AvatarForm />
              </div>
            </div>
            {/* <!-- End Basic Modal--> */}
          </div>

          <div className="col-xl-8">
            <div className="card">
              <div className="card-body pt-3">
                {/* <!-- Bordered Tabs --> */}
                <ul className="nav nav-tabs nav-tabs-bordered">
                  <li className="nav-item">
                    <button
                      className="nav-link active"
                      data-bs-toggle="tab"
                      data-bs-target="#profile-overview"
                    >
                      Profile Information
                    </button>
                  </li>
                  {!isAdmin && (
                    <>
                      <li className="nav-item">
                        <button
                          className="nav-link"
                          data-bs-toggle="tab"
                          data-bs-target="#kyc-verification"
                        >
                          KYC verification
                        </button>
                      </li>
                      <li className="nav-item">
                        <button
                          className="nav-link"
                          data-bs-toggle="tab"
                          data-bs-target="#profile-change-password"
                        >
                          Change Password
                        </button>
                      </li>
                    </>
                  )}
                </ul>
                <div className="tab-content pt-2">
                  <div
                    className="tab-pane fade show active profile-overview"
                    id="profile-overview"
                  >
                    <div className="row">
                      <div className="col-lg-3 col-md-4 label ">Full Name</div>
                      <div className="col-lg-9 col-md-8">
                        {dash?.owner?.firstName} {dash?.owner?.lastName}
                      </div>
                    </div>

                    <div className="row">
                      <div className="col-lg-3 col-md-4 label">Email</div>
                      <div className="col-lg-9 col-md-8">
                        {dash?.owner?.email}
                      </div>
                    </div>

                    <div className="row">
                      <div className="col-lg-3 col-md-4 label">
                        Investment type
                      </div>
                      <div className="col-lg-9 col-md-8">
                        {dash?.investment?.investmentPlan ?? "N/A"}
                      </div>
                    </div>

                    <div className="row">
                      <div className="col-lg-3 col-md-4 label">
                        Investment package
                      </div>
                      <div className="col-lg-9 col-md-8">
                        {dash?.investment?.investmentPackage ?? "N/A"}
                      </div>
                    </div>

                    {adminActions}
                  </div>

                  {!isAdmin && (
                    <>
                      <div className="tab-pane fade pt-3" id="kyc-verification">
                        <KYCForm />
                      </div>
                      <div
                        className="tab-pane fade pt-3"
                        id="profile-change-password"
                      >
                        <ChangePasswordForm />
                      </div>
                    </>
                  )}
                </div>
                {/* <!-- End Bordered Tabs --> */}
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Profile;
